@import url(https://fonts.googleapis.com/css?family=Anton|Lobster|Poppins);
html {
  font-size: 14px;
}
body {
  background-color: #f7f7f7;
}
.color-text-goomec {
  color: #ff0000;
}
.material-icons {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 24px; /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  -webkit-font-feature-settings: "liga";
          font-feature-settings: "liga";
}

.pulse {
  cursor: pointer;
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite;
}
.pulse:hover {
  -webkit-animation: none;
          animation: none;
}
@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.4);
  }
  70% {
    -webkit-box-shadow: 0 0 0 10px rgba(204, 169, 44, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
  }
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.4);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(204, 169, 44, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0);
  }
}

@font-face {
  font-family: "Material Icons";
  font-style: normal;
  font-weight: 400;
  src: url(/static/media/MaterialIcons-Regular.e79bfd88.eot); /* For IE6-8 */
  src: local("Material Icons"), local("MaterialIcons-Regular"),
    url(/static/media/MaterialIcons-Regular.570eb838.woff2) format("woff2"),
    url(/static/media/MaterialIcons-Regular.012cf6a1.woff) format("woff"),
    url(/static/media/MaterialIcons-Regular.a37b0c01.ttf) format("truetype");
}

.josefin-font {
  font-family: "Poppins", sans-serif;
}

.Cursor--blinking {
  opacity: 1;
  -webkit-animation: blink 1s linear infinite;
          animation: blink 1s linear infinite;
}
.App {
  text-align: center;
}

.App-loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
}

@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.capa-site {
  opacity: 0.5;
}
.bg-gradient {
  height: 35.8em;
  background: #e52f1e; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #4286f4,
    #e52f1e
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    left,
    #4286f4,
    #e52f1e
  );
  background: linear-gradient(
    to right,
    #4286f4,
    #e52f1e
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
.display-flex {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.slogan {
  font-family: "Anton", sans-serif;
  position: relative;
  bottom: 32em;
  height: 0.8em;
}
.slogan h1 {
  color: #e82c0c !important;
  letter-spacing: 0.1em;
}
.text-shadown {
  text-shadow: 4px 4px 4px rgba(0, 0, 0, 0.8);
}
.sub-slogan {
  color: #424242 !important;
  font-family: "Anton", sans-serif;
  line-height: 126%;
  font-size: 2.5em;
  margin-bottom: 1em;
  letter-spacing: 0.1em;
}
.sub-slogan h3 {
  font-size: 2em;
}
.text-about {
  line-height: 1.5;
  font-size: 2em;
}

.center-align h1 {
  font-size: 10em;
}
@media screen and (max-width: 768px) {
  .sub-slogan h3 {
    margin-top: 1em;
    font-size: 1em;
  }
  .text-about {
    font-size: 1.5em;
  }
  .parallax-container {
    height: 440px;
  }
  .bg-gradient {
    height: 31.5em;
  }
  .parallax img {
    width: 30em;
  }
}

@media screen and (max-width: 600px) {
  .slogan h1 {
    font-size: 6em;
  }
  .slogan {
    bottom: 30em;
  }
  .sub-slogan {
    font-size: 1.4em;
  }
  .text-about {
    text-align: justify;
    font-size: 1.2em;
  }
  .parallax img {
    width: 36em;
  }
}
@media screen and (max-width: 375px) {
  .sub-slogan h3 {
    margin-top: 1em;
    font-size: 1.5em;
  }
}

.thumbnail {
  display: flex;
  justify-content: center;
  border-radius: 100%;
  height: 35%;
  width: 100%;
  background-repeat: no-repeat;
  background-size: 200px;
  border: 1px outset rgba(0, 0, 0, 0.3);
  box-shadow: 10px 10px 15px -5px rgba(0, 0, 0, 0.55);
}
.small {
  height: 13em;
  width: 13em;
}
.align-item-flex {
  display: flex;
  justify-content: center;
}
.carousel {
  -webkit-perspective: 16em;
          perspective: 16em;
  height: 23em;
}
.card {
  height: 80%;
}
.button-market {
  margin-top: 0;
}
/* @media screen and (max-width: 768px) {
  .card {
    height: 90%;
  }
} */

.fixed-button {
  position: fixed;
  bottom: 3%;
  right: 2%;
  border-radius: 2em 2em;
}
.qualidade {
  background-image: url(/static/media/selo_qualidade.89048c39.png);
  background-size: 40%;
  background-position-x: 50%;
  background-position-y: 30%;
  background-repeat: no-repeat;
  height: 50%;
}
.text-justify {
  text-align: justify;
  font-size: 1.5em;
}

.item {
  display: block;
  -webkit-transition: 0.25s;
  transition: 0.25s;
  color: #26a69a;
}
.item:hover {
  background-color: #218a7f41;
}

@media screen and (max-width: 992px) {
  .fixed-button {
    opacity: 0.8;
    height: 40px !important;
    width: 94px !important;
    padding: 0em 1em !important;
  }
  .fixed-button:hover {
    opacity: 1;
  }
  .text-anuncio {
    font-size: 1.5em;
  }
}

/* On screens that are 600px wide or less, the background color is olive */
@media screen and (max-width: 600px) {
  .fixed-button {
    opacity: 0.8;
  }
  .fixed-button:hover {
    opacity: 1;
  }
  .sub-slogan h2 {
    font-size: 2em;
  }
  .text-anuncio {
    font-size: 1.5em;
  }
}

.modal {
  font-family: Josefin Sans, sans-serif;
  margin-top: -2em;
  max-height: 85%;
}
.modal.modal-fixed-footer {
  width: 90%;
  height: 90%;
}
.text-details {
  margin: 2em;
}
.text-products {
  text-align: left;
}
.products {
  height: 100%;
  -webkit-transition: 0.8s;
  transition: 0.8s;
}
.no-repeat-background {
  background-repeat: no-repeat;
  background-size: 100%;
  background-position-x: 50%;
}
.content-block {
  border-radius: 0.5em 0.5em;
  border: 1px outset rgba(0, 0, 0, 0.3);
  box-shadow: 10px 10px 15px -5px rgba(0, 0, 0, 0.55);
}
.center-item {
  padding: 2em !important;
}

